import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteModule } from '../../route/route.module';
import { RouteComponent } from '../../route/route/route.component';
import { ThreadComponent } from './thread/thread.component';
import { ThreadsComponent } from './threads/threads.component';

const routes: Routes = [
  {
    path: '',
    component: ThreadsComponent,
  },
  {
    path: ':pageid/:fbid',
    component: RouteComponent,
    data: { wrappedComponent: ThreadComponent },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes), RouteModule],
  exports: [RouterModule],
  entryComponents: [ThreadComponent],
})
export class ThreadsRoutingModule {}
